import React, { Component } from 'react';
import './App.css';
import LineaTiempo from './components/LineaTiempo';
import FormAdd from './components/FormAdd';

class App extends Component {
  constructor(){
    super();
    this.state={
      inicial:0,
      final:0
    };
    this.inicialChange=this.inicialChange.bind(this);
    this.finalChange=this.finalChange.bind(this);
  }

  inicialChange(e){
    var inicialval=e.target.value;
    this.setState({
      inicial: inicialval
    });
  }
  finalChange(e){
    var finalval=e.target.value;
    this.setState({
      final: finalval
    });
  }

  
  render() {
    return (
      <div className="contenedorTiempo">
        <div className="espacioTrabajo">
          <FormAdd/>
          <label for="inicial">Año inicial</label>
          <input type="number" id="inicial" onChange={this.inicialChange}/>
          <LineaTiempo inicio={this.state.inicial} final={this.state.final}/>
          <input type="number" id="final" onChange={this.finalChange}/>
          <label for="inicial">Año final</label>
        </div>
      </div>
    );
  }
}

export default App;
