import React, { Component } from 'react';

class Elemento extends Component{
    constructor(props){
        super(props);
        this.handleDel=this.handleDel.bind(this);
    }
    handleDel(){
        this.props.ondel(this.props.id);
    }
    render(){
        var location=this.props.left + "%";
        const styles={
            left: location
        };
        return(
            <div className="elemento" id={this.props.id} style={styles}>
                <div className="contenido">
                    <h6>{this.props.nombre}</h6>
                    <img src={this.props.img} title={this.props.nombre} alt={this.props.nombre}/>
                    <span className="fechaElemento">{this.props.fecha}</span>
                    <button onClick={this.handleDel}>Eliminar</button>
                </div>
                <i class="fas fa-circle" title={this.props.fecha}></i>
            </div>
        );
    }
}

export default Elemento;